body {
  margin: auto 0;
  padding: 0;
  background-image: url('https://images.unsplash.com/photo-1518972559570-7cc1309f3229?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80');
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'SEGA LOGO FONT';
  font-style: normal;
  font-weight: normal;
  src: local('SEGA LOGO FONT'), url(/Top-Electro-Artists/static/media/SEGA.12a3db8b.woff) format('woff');
}

.load {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px !important;
}

.home {
  padding-top: 12px;
  font-family: 'SEGA LOGO FONT';
  font-size: 32px;
  display: inline-block;
  background-image: linear-gradient(to right, #ffff4d, #33ff33);
  -webkit-background-clip: text;
  color: transparent;
}

.home:link {
  padding-top: 12px;
  font-family: 'SEGA LOGO FONT';
  font-size: 32px;
  display: inline-block;
  background-image: linear-gradient(to right, #ffff4d, #33ff33);
  -webkit-background-clip: text;
  color: transparent;
}

@media (max-width: 600px) {
  .home {
    font-size: 28px;
  }
}

a {
  color: aqua;
}

a:hover {
  text-decoration: none;
  color: rgb(80, 160, 235);
}

h6 {
  color: antiquewhite !important;
}

.main-container {
  background-image: linear-gradient(
    to bottom right,
    rgba(77, 0, 0, 0.8),
    rgba(0, 0, 102, 0.8)
  );
  color: antiquewhite;
  padding: 20px 20px;
  border-radius: 8px;
  margin: 20px;
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
}

.box2 {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.nam {
  margin-top: 12px;
  margin-bottom: 20px;
}

.nat {
  margin-top: 20px;
  color: antiquewhite !important;
}

.bio {
  overflow-y: scroll;
  height: 140px;
}

@media (max-width: 474px) {
  .image {
    width: 240px !important;
  }
  .nam {
    font-size: 24px;
  }

  .gen {
    font-size: 18px;
  }

  .bio {
    width: 96%;
  }
}

@media (min-width: 1305px) {
  .main-container {
    grid-template-columns: 1fr 1fr 2fr;
  }
}

@media (min-width: 685px) and (max-width: 768px) {
  .image {
    width: 284px !important;
  }
}

@media (min-width: 685px) and (max-width: 994px) {
  .bio {
    width: 207%;
  }
}

@media (min-width: 995px) and (max-width: 1100px) {
  .image {
    width: 284px !important;
  }
}

.navbar {
  padding: 12px 20px !important;
  background-color: grey;
  opacity: 0.9;
  width: 100%;
}

.navbar-toggler {
  margin-bottom: 10px;
}

.logo {
  margin: 0 50px;
}

input {
  opacity: 0.8;
}

