  .navbar {
    margin-bottom: 30px !important;
  }

  .home {
    margin-left: 0;
    margin-top: -10px;
    font-size: 28px !important;
  }

  .logo {
    margin: 0 24px;
  }

  input {
    position: absolute;
    top: 80px;
    right: 35%;
  }
}

